import React from "react";
// import Sticker from "./Sticker";

import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import Typography from "@mui/material/Typography";
import Grid from "@mui/material/Grid";
import CloseIcon from "@mui/icons-material/Close";
import CardHeader from "@mui/material/CardHeader";
import IconButton from "@mui/material/IconButton";
import EditIcon from "@mui/icons-material/Edit";
// import FavoriteIcon from "@mui/icons-material/Favorite";

class AllStickers extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      allData: [],
      id: 0,
      counter: 100,
    };
  }

  componentDidMount() {
    console.log("[componentDidMount - AllStickers]");
    const temp = [...this.props.myData];
    this.setState((data) => {
      return {
        ...data,
        allData: temp,
      };
    });
  }

  componentDidUpdate() {
    console.log("[componentDidUpdate - AllStickers]", this.props);
    if (
      JSON.stringify(this.props.myData) !== JSON.stringify(this.state.allData)
    ) {
      const temp = [...this.props.myData];
      this.setState((data) => {
        return {
          ...data,
          allData: temp,
        };
      });
    }
  }

  // shouldComponentUpdate(nextProps, nextState) {
  //   if (nextProps.myData.length === this.state.allData.length) {
  //     return false;
  //   } else {
  //     const temp = [...nextProps.myData];
  //     this.setState((data) => {
  //       return {
  //         ...data,
  //         allData: temp,
  //       };
  //     });
  //     return true;
  //   }
  // }

  updateId = () => {
    this.setState((data) => {
      return {
        ...data,
        id: this.state.id + 1,
      };
    });
  };

  // componentDidUpdate() {
  //   console.log("componentDidUpdate - AllStickers");
  // }

  render() {
    return (
      <div>
        {/* <button onClick={this.updateId}>Test {this.state.id}</button> */}
        <Grid
          container
          direction="row"
          justifyContent="flex-start"
          alignItems="flex-start"
          // style={{ backgroundColor: "#333" }}
          paddingLeft={"15px"}
        >
          <Grid
            xs={12}
            padding={2}
            direction="row"
            container
            spacing={2}
            paddingBottom={10}
          >
            {this.state.allData && this.state.allData.length > 0 ? (
              this.state.allData.map((el, i) => {
                return (
                  <Grid item xs={3}>
                    <Card
                      sx={{
                        maxWidth: 330,
                        height: 300,
                        backgroundColor: "#D8D8D8",
                      }}
                    >
                      <CardHeader
                        style={{ backgroundColor: "#565555", color: "#fff" }}
                        title={el.value}
                        subheader={el.number}
                        action={
                          <>
                            <IconButton aria-label="add to favorites">
                              <EditIcon
                                sx={{ color: "white" }}
                                onClick={() => this.props.editCardref(el.id)}
                              />
                            </IconButton>
                            <IconButton aria-label="add to favorites">
                              <CloseIcon
                                sx={{ color: "white" }}
                                onClick={() => this.props.deleteCardref(el.id)}
                              />
                            </IconButton>
                          </>
                        }
                      />

                      <React.Fragment key={`key_${i}`}>
                        <Typography
                          gutterBottom
                          variant="h5"
                          component="div"
                          style={{
                            color: "#333",
                            textAlign: "center",
                            marginTop: "20px",
                            padding: "5px",
                          }}
                        >
                          {el.message}
                        </Typography>
                      </React.Fragment>
                    </Card>
                  </Grid>
                );
              })
            ) : (
              <Grid container spacing={2} padding={3}>
                <Grid item xs={12}>
                  <Typography variant="subtitle2" gutterBottom component="div">
                    Welcome to App, designed and develped only for demo purpose.
                  </Typography>
                </Grid>
                <Grid item item xs={12}>
                  <Typography variant="subtitle2" gutterBottom component="div">
                    Card add unlimited numbers and todo card by clicking (+)
                    icon at right hand side bottom of the application.
                  </Typography>
                </Grid>
                <Grid item item xs={12}>
                  <Typography variant="subtitle2" gutterBottom component="div">
                    You can have title date and message to remember
                  </Typography>
                </Grid>
                <Grid item item xs={12}>
                  <Typography variant="subtitle2" gutterBottom component="div">
                    Only title field is mandate.
                  </Typography>
                </Grid>
              </Grid>
            )}
          </Grid>
        </Grid>
      </div>
    );
  }
}
export default AllStickers;
