import React from "react";
import Grid from "@mui/material/Grid";

import Box from "@mui/material/Box";
import Modal from "@mui/material/Modal";
import Fade from "@mui/material/Fade";
// import Button from "@mui/material/Button";
// import Typography from "@mui/material/Typography";
import AddCircleIcon from "@mui/icons-material/AddCircle";
import IconButton from "@mui/material/IconButton";
// import { TextField } from "@material-ui/core";
// import { inputAdornmentClasses } from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";
import Chip from "@mui/material/Chip";
import Stack from "@mui/material/Stack";

class InputItem extends React.Component {
  constructor() {
    super();
    this.state = {
      previousId: 0,
      inputData: {
        id: 0,
        value: "",
        number: "",
        message: "",
      },
      editData: {},
      editFlag: false,
      open: false,
    };
  }

  handleOpen = () => {
    // this.setOpen(true);

    const temp = { ...this.state };
    temp.open = true;
    this.setState(temp);
  };

  handleClose = () => {
    const temp = { ...this.state };
    temp.open = false;
    this.setState(temp);
  };

  onChangeHandler = (e) => {
    const baseObejct = { ...this.state };
    const temp = { ...this.state.inputData };
    temp.value = e.target.value;
    baseObejct.inputData = temp;
    this.setState(baseObejct);
  };
  onChangeHandlerNumber = (e) => {
    const baseObejct = { ...this.state };
    const temp = { ...this.state.inputData };
    temp.number = e.target.value;
    baseObejct.inputData = temp;
    this.setState(baseObejct);
  };

  onChangeHandlerMessage = (e) => {
    const baseObejct = { ...this.state };
    const temp = { ...this.state.inputData };
    temp.message = e.target.value;
    baseObejct.inputData = temp;
    this.setState(baseObejct);
  };

  onChangeHandlerEdit = (e) => {
    const baseObejct = { ...this.state };
    const temp = { ...this.state.editData };
    temp.value = e.target.value;
    baseObejct.editData = temp;
    this.setState(baseObejct);
  };

  onChangeHandlerNumberEdit = (e) => {
    const baseObejct = { ...this.state };
    const temp = { ...this.state.editData };
    temp.number = e.target.value;
    baseObejct.editData = temp;
    this.setState(baseObejct);
  };
  onChangeHandlerMessageEdit = (e) => {
    const baseObejct = { ...this.state };
    const temp = { ...this.state.editData };
    temp.message = e.target.value;
    baseObejct.editData = temp;
    this.setState(baseObejct);
  };
  onFormHandler = (e) => {
    // state object copy
    const tempInitialObejct = { ...this.state };
    // state child object copy
    const tempInpData = { ...this.state.inputData };
    // operation in child object
    tempInpData.id = tempInitialObejct.previousId + 1;
    // operation in state object
    tempInitialObejct.previousId = tempInpData.id;
    // function call to parent function
    // console.log("tempInpData", tempInpData);
    this.props.updateStkrFuncRef({ ...tempInpData });
    // operation in child object
    tempInitialObejct.open = false;
    tempInpData.value = "";
    tempInpData.id = 0;
    tempInpData.number = "";
    tempInpData.message = "";
    //operation in state object
    tempInitialObejct.inputData = tempInpData;
    // update to state object
    this.setState(tempInitialObejct);
  };

  onFormHandlerUpdate = (e) => {
    const tempInitialObejct = { ...this.state };
    const tempupdateData = { ...this.state.editData };
    this.props.handleUpdatedCard({ ...tempupdateData });
    tempInitialObejct.editData = {};
    tempInitialObejct.editFlag = false;
    tempInitialObejct.open = false;
    this.setState(tempInitialObejct);
  };

  componentDidMount() {
    console.log("[componentDidMount - InputItem.js]");
  }

  componentDidUpdate() {
    if (
      JSON.stringify(this.props.editData) !==
        JSON.stringify(this.state.editData) &&
      this.props.editData.hasOwnProperty("id") &&
      this.state.editFlag === false
    ) {
      this.setState((data) => {
        return {
          ...data,
          editData: { ...this.props.editData },
          editFlag: true,
          open: true,
        };
      });
    }
  }

  Test1 = () => {
    alert("Hello");
  };

  btnstyle = {
    height: "45px",
    width: "100%",
    backgroundColor: "#8B008B",
    marginLeft: "10px",

    border: "none",
    color: "white",
  };
  txtstyle = {
    height: "40px",
    width: "100%",
    paddingLeft: "20px",
  };
  style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 400,
    height: 300,
    bgcolor: "background.paper",
    border: "2px solid #000",
    boxShadow: 24,
    padding: 10,
    paddingTop: "20px",
    float: "right",
  };

  render() {
    return (
      <>
        <div
          style={{
            position: "fixed",
            float: "right",
            top: "65%",
            marginLeft: "90%",
          }}
        >
          <Grid
            container
            direction="row"
            justifyContent="center"
            alignItems="center"
            paddingTop={"80px"}
            paddingBottom={"40px"}
          >
            <Stack direction="row">
              <Chip
                avatar={
                  <IconButton
                    size={"large"}
                    color="secondary"
                    aria-label="Add ToDo"
                  >
                    <AddCircleIcon
                      fontSize="large"
                      sx={{ color: "red" }}
                      onClick={this.handleOpen}
                    />
                  </IconButton>
                }
                color={"error"}
                label="Add Todo"
                variant="outlined"
                style={{ backgroundColor: "#fff" }}
              />
            </Stack>
            <Modal
              aria-labelledby="transition-modal-title"
              aria-describedby="transition-modal-description"
              open={this.state.open}
              // onClose={this.handleClose}
              closeAfterTransition
              // BackdropComponent={Backdrop}
            >
              <Fade in={this.state.open}>
                <Box sx={this.style}>
                  <IconButton aria-label="add to favorites">
                    <CloseIcon
                      onClick={this.handleClose}
                      style={{ marginLeft: "400px" }}
                    />
                  </IconButton>
                  {this.state.editData &&
                  this.state.editData.hasOwnProperty("id") &&
                  this.state.editData.id !== "" ? (
                    <>
                      <Grid item xs={12} marginY={2}>
                        <input
                          style={this.txtstyle}
                          type="text"
                          value={this.state.editData.value}
                          placeholder="Add Todo List"
                          onChange={this.onChangeHandlerEdit}
                        />
                      </Grid>
                      <Grid item xs={12} marginY={2}>
                        <input
                          style={this.txtstyle}
                          type="date"
                          value={this.state.editData.number}
                          onChange={this.onChangeHandlerNumberEdit}
                        />
                      </Grid>
                      <Grid item xs={12} marginY={2}>
                        <textarea
                          style={this.txtstyle}
                          rows="11"
                          cols="100"
                          name="comment"
                          form="usrform"
                          placeholder="Message here..."
                          value={this.state.editData.message}
                          onChange={this.onChangeHandlerMessageEdit}
                        ></textarea>
                      </Grid>
                      <button
                        style={this.btnstyle}
                        onClick={this.onFormHandlerUpdate}
                        disabled={this.state.editData.value === "" && true}
                      >
                        Update Todos
                      </button>
                    </>
                  ) : (
                    <>
                      <Grid item xs={12} marginY={2}>
                        <input
                          style={this.txtstyle}
                          type="text"
                          value={this.state.inputData.value}
                          placeholder="Add Todo List"
                          onChange={this.onChangeHandler}
                        />
                      </Grid>
                      <Grid item xs={12} marginY={2}>
                        <input
                          style={this.txtstyle}
                          type="date"
                          value={this.state.inputData.number}
                          onChange={this.onChangeHandlerNumber}
                        />
                      </Grid>
                      <Grid item xs={12} marginY={2}>
                        <textarea
                          style={this.txtstyle}
                          rows="11"
                          cols="100"
                          name="comment"
                          form="usrform"
                          placeholder="Message here..."
                          value={this.state.inputData.message}
                          onChange={this.onChangeHandlerMessage}
                        ></textarea>
                      </Grid>
                      <Grid item xs={12} marginY={2}>
                        <button
                          style={this.btnstyle}
                          onClick={this.onFormHandler}
                          disabled={this.state.inputData.value === "" && true}
                        >
                          Add Todos
                        </button>
                      </Grid>
                    </>
                  )}
                </Box>
              </Fade>
            </Modal>
          </Grid>
        </div>
      </>
    );
  }
}
export default InputItem;
