import React from "react";
// import Grid from "@mui/material/Grid";
import InputComponent from "../components/InputItem";
import AllStickers from "../components/AllStickers";
import Grid from "@mui/material/Grid";
import AppBar from "@mui/material/AppBar";
import Typography from "@mui/material/Typography";
import Toolbar from "@mui/material/Toolbar";

class Home extends React.Component {
  constructor() {
    super();
    this.state = {
      allStickers: [],
      editTodoCard: {},
    };
  }

  componentDidMount() {
    // console.log("[componentDidMount - Home.js]");
  }

  componentDidUpdate() {
    console.log("componentDidUpdate - Home", this.state);
  }

  updateStickers = (data) => {
    const tempObejct = { ...this.state };
    // console.log("tempObejct", tempObejct);
    const tempArray = [...this.state.allStickers];
    tempArray.push(data);
    tempObejct.allStickers = tempArray;
    this.setState(tempObejct);
  };

  handleUpdatedCard = (data) => {
    let tempUpdateArray = [...this.state.allStickers];

    const index = tempUpdateArray.findIndex((el) => el.id === data.id);
    tempUpdateArray[index] = data;

    this.setState((data) => {
      return {
        ...data,
        allStickers: tempUpdateArray,
        editTodoCard: {},
      };
    });
  };

  editCard = (objectId) => {
    // const tempeditData = { ...this.state };
    const tempStateArray = { ...this.state };
    const tempdata = tempStateArray.allStickers.find(
      (el) => el.id === objectId
    );
    tempStateArray.editTodoCard = tempdata;
    this.setState(tempStateArray);
  };

  deleteCard = (deleteId) => {
    const tempdeleteArray = { ...this.state };
    const tempdeletedata = tempdeleteArray.allStickers.filter(
      (el) => el.id !== deleteId
    );
    tempdeleteArray.allStickers = tempdeletedata;
    // return tempdeleteArray;
    this.setState(tempdeleteArray);
  };

  render() {
    return (
      <div>
        <AppBar position="static" sx={{ backgroundColor: "#8B008B" }}>
          <Toolbar>
            <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
              Todo App
            </Typography>
          </Toolbar>
        </AppBar>
        <AllStickers
          myData={this.state.allStickers}
          editCardref={this.editCard}
          deleteCardref={this.deleteCard}
        />
        <InputComponent
          updateStkrFuncRef={this.updateStickers}
          editData={this.state.editTodoCard}
          handleUpdatedCard={this.handleUpdatedCard}
        />
        <AppBar
          position="fixed"
          sx={{ top: "auto", bottom: 0, backgroundColor: "#242424" }}
        >
          <Toolbar>
            <Typography
              variant="h8"
              component="div"
              sx={{
                flexGrow: 0,
                margin: "auto",
              }}
            >
              Designed and Developed by Vidya Bharti
            </Typography>
          </Toolbar>
        </AppBar>
      </div>
    );
  }
}
export default Home;
